html, body {
  /* position: fixed; */
  /* overflow: hidden; */

}

.App {
  position: relative;
  text-align: center;
  min-height: 200px;
  min-width: 390px;
  max-width: 600px;
  background-color: #e4e4e4;
  overflow: hidden;
  margin: auto;
  font-family: Ruda, sans-serif;
  line-height: normal;
}

@media only screen and (max-width: 400px) {
  .App {
    max-width: 400px;
    width: 400px;
  }
}

.App-header {
  background-color: #ffffff;
  min-height: 50px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(9px + 2vmin);
  color: #04091e;
  padding-bottom: 20px;
  overflow: hidden;
  vertical-align: middle;
  position: relative;
}

@media only screen and (max-width: 600px) {
  .App-header {
    width: 800px;
    left: -100px;
    overflow: hidden;
    transition: left 2s;
  }
  .App-main {
    top: 100px;
  }
}

.App-main {
  /* background-image: url("./header1-bg.png"); */
  background-color: #ffffff;
  background-size: 100%;
  background-repeat: no-repeat;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  top: 0px;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  /* enables “momentum” (smooth) scrolling */
}

.App-footer {
  background-color: #ffffff;
  min-height: 50px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(9px + 2vmin);
  color: #f20253;
  overflow: hidden;
  position: relative;
  bottom: calc(-100% + 50px);
}

button {
  min-width: 40px;
  min-height: 40px;
  border-style: none;
  background: #f20253;
  Color: white;
  /* font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif; */
  /* margin-left:15px; */
}

button:disabled {
  background: #ce7a96;
}

button>span {
  min-width: 40px;
  min-height: 40px;
  font-weight: bold;
}

.hide {
  visibility: hidden;
}

.SubjectInformationForm {
  position: absolute;
  top: 50%;
  left: 50%;
  height: 30%;
  width: 50%;
  margin: -15% 0 0 -25%;
}

.DownloadForm {
  position: absolute;
  top: 50%;
  left: 50%;
  height: 30%;
  width: 50%;
  margin: -15% 0 0 -25%;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.noselect {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -khtml-user-select: none;
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently
  supported by Chrome and Opera */
}

.nonBlocking {
  color: rgba(255, 255, 255, 0.15);
  font-size: 80px;
  position: fixed;
  top: 150px;
  left: 100px;
  -ms-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  transform: rotate(30deg);
  pointer-events: none;
  /* font-family: Impact, Charcoal, sans-serif; */
}

.formContainer {
  /* position: absolute; */
  width: 70%;
  display: flex;
  flex-direction: column;
  align-self: center;
  position: relative;
  top: 0px;
  /* border-style: dotted;
  border-width: 1px;
  border-color: blue; */
}

.chevron.inactive {
  position: relative;
  margin-top: 20px;
  background: url("./chevron.svg");
  height: 42px;
  width: 117px;
  font-size: 12px;
  display: block;
  text-align: center;
}

.chevron.active {
  position: relative;
  margin-top: 20px;
  background: url("./chevronactive.svg");
  color: white;
  height: 42px;
  width: 117px;
  font-size: 12px;
  display: block;
  text-align: center;
}

.chevron>span {
  position: absolute;
  top: 50%;
  left: 50%;
  height: 30%;
  width: 50%;
  margin: -13% 0 0 -25%;
}

input {
  width: 100%;
  margin-bottom: 7px;
  font-size: 16px;
}

.stepButtons {
  display: block;
  width: 80%;
}

.stepButtonleft {
