.overlay {
  z-index: 900;
  position: absolute;
  background-color: rgba(100, 100, 100, 100);
  width: 600px;
  height: 300px;
  opacity: 0.8;
  display: flex;
  align-items: center;
  justify-content: center;
}

.lds-dual-ring {
  z-index: 910;
  display: block;
  width: 64px;
  height: 64px;
  align-self: center;
}

.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #f20253;
  border-color: #f20253 transparent #f20253 transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}

@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

